.session {
  color: white;
  background-color: var(--primary);
  border-radius: 5px;
  height: 200px;
  width: 200px;
  &_title {
    opacity: 0.8;
    position: absolute;
    bottom: 191px;
    left: 127px;
    width: 160px;
  }
  &_lineChart {
    &_YxLeftTime {
      display: none;
    }
    &_line {
      & circle {
        display: none;
      }
    }
    &_XxDay {
      & line {
        display: none;
      }
    }
  }
}
